<template>
  <el-dialog
    :title="title"
    top="5vh"
    :visible="value"
    :show-close="true"
    width="920px"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-scrollbar class="scroll-dialog">
      <div class="main-box">
        <!-- 头部搜索 -->
        <el-form ref="filterItem" :model="query" inline @submit.native.prevent>
          <el-form-item prop="sourceType">
            <el-select
              v-model="query.sourceType"
              placeholder="来源类型"
              class="filter-item"
              style="width: 178px;"
              filterable
              clearable
              @change="searchInit"
            >
              <el-option
                v-for="(item,index) in dict['wms_out_warehouse_bill_type']"
                :key="index"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
          <el-form-item prop="sourceBillNumber">
            <el-input
              v-model.trim="query.sourceBillNumber"
              placeholder="来源单号"
              class="filter-item"
              style="width: 178px;"
              clearable
              @keyup.enter.native="searchInit"
            />
          </el-form-item>
          <el-form-item prop="stockOutNumber">
            <el-input
              v-model.trim="query.stockOutNumber"
              placeholder="出库单号"
              class="filter-item"
              style="width: 178px;"
              clearable
              @keyup.enter.native="searchInit"
            />
          </el-form-item>
          <el-button
            class="filter-item"
            type="success"
            icon="el-icon-search"
            @click="searchInit"
          >搜索</el-button>
          <el-button
            class="filter-item"
            type="warning"
            icon="el-icon-refresh-left"
            @click="resetForm('filterItem')"
          >重置</el-button>
        </el-form>
        <el-row>
          <table-com
            :key="refreshKey"
            :ref="refName"
            :table-height-type="490"
            :loading="loading"
            :index="true"
            :index-fixed="false"
            :selection="true"
            :multiple="multiple"
            :selection-fixed="false"
            :table-column-sort="false"
            :table-data="tableData"
            :column-list="columnList"
            :show-operate="false"
            :operate-fixed="false"
            :table-selection="tableSelection"
            @sortChange="handleSortChange"
            @selectionChange="selectionChange"
          />
        </el-row>
        <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
      </div>
    </el-scrollbar>
    <div slot="footer">
      <el-button type="primary" @click="confirmDialog">确 定</el-button>
      <el-button @click="beforeClose">取 消</el-button>
    </div>
  </el-dialog>
</template>

<script>
import { v4 as uuidv4 } from 'uuid'
import paginationPage from '@/mixins/paginationPage'
import { getAddPagelistApi as getListSelectApi } from '@/apis/modules/out-bound/stock-return'
const INITQUERYFORM = () => ({
  sourceType: '',
  sourceBillNumber: '',
  stockOutNumber: ''
})

export default {
  name: 'MaterialSelect',
  mixins: [paginationPage],
  props: {
    value: {
      required: true,
      type: Boolean
    },
    title: {
      type: String,
      default: '选择出库单'
    },
    // 是否多选，如果多选，返回id的数组， 如果单选返回 {id, name}
    multiple: {
      type: Boolean,
      default: true
    },
    // 默认勾选的ids
    selection: {
      type: Array,
      default: () => []
    },
    // 多选状态下的最大选择数量
    max: {
      type: Number,
      default: 0
    },
    ids: {
      type: Array,
      default: () => []
    },
    extraInfo: {
      type: Object,
      default: () => {}
    }
  },
  dicts: ['wms_out_warehouse_bill_type'],
  data() {
    return {
      loading: false,
      refName: 'modelSelectMaterialSelect',
      query: INITQUERYFORM(),
      tableData: [],
      columnList: [
        {
          module: 'status',
          prop: 'sourceType_dictText',
          label: '来源类型',
          align: 'center',
          // width: 120,
          // minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: (prop) => {
            return prop
          },
          headerAlign: 'center',
          statusFormatter: (scope) => {
            const typeMap = {
              '1': 'success',
              '2': 'primary',
              '3': 'warning',
              '4': 'info'
            }
            return scope.sourceType ? typeMap[scope.sourceType] : ''
          }
        },
        {
          module: 'word',
          prop: 'sourceBillNumber',
          label: '来源单号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'stockOutNumber',
          label: '出库单号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'date',
          label: '出库日期',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: 'custom',
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      columnSets: [],
      tableSelection: [],
      deptSelectedInfo: {
        list: [],
        visible: false,
        radio: ''
      },
      refreshKey: 0,
      orderBy: '' // 字段排序信息字符串
    }
  },
  methods: {
    initSelect() {
      this.init()
    },
    init() {
      const params = {
        ...this.query,
        ...this.extraInfo,
        ...this.orderBy && { orderBy: this.orderBy },
        page: {
          ...this.pageInfo
        }
      }
      return new Promise((resolve, reject) => {
        getListSelectApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { list, ...pageInfo } = data
            this.initPage(pageInfo)
            this.tableData = list.map(i => {
              return {
                uuid: uuidv4(),
                deliveryNumber: i.orderNum,
                ...i
              }
            })
            // 初始化selection
            this.tableSelection = this.tableData.filter(i => this.selection.includes(i.id))
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleSortChange({ column, prop, order }) {
      if (prop !== 'date') return
      const obj = {
        descending: 'desc',
        ascending: 'asc'
      }
      this.orderBy = order ? `${prop} ${obj[order]}` : ''
      this.orderBy && this.init()
    },
    selectionChange(tableSelection) {
      this.tableSelection = [...tableSelection]
    },
    // 更新列表复选框勾选
    handleUpdateTableSelected(type = '') {

    },
    handleDefault() {
      this.tableSelection = new Array(0)
    },
    confirmDialog() {
      if (!this.tableSelection.length) {
        this.$message({
          message: '请选择出库单',
          type: 'warning'
        })
        return
      }
      this.$emit('confirm', this.tableSelection)
      this.beforeClose()
    },
    beforeClose(done) {
      this.$emit('input', false)
      this.handleDefault()
      typeof done === 'function' && done()
    },
    onDeptSelectConfirm(list) {
      if (list.length) {
        this.query.usedOffice = list[0].name
      }
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.query = INITQUERYFORM()
      this.orderBy = ''
      this.refreshKey++
      this.searchInit()
    }
  }
}
</script>

<style lang="scss" scoped>
.scroll-dialog {
  height: 600px;
}
.main-box {
  padding: 10px;
  box-sizing: border-box;
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}
 ::v-deep .el-dialog__body {
  padding-top: 10px;
  padding-bottom: 0;
}
</style>
